import React from "react";

export function CommonHead(props) {

    const description = props?.data?.page?.description || props.data.page.name;

    const path = props?.data?.page.path;
    const name = props?.data?.page?.name;
    console.log("head", props?.data?.page, name);
    const title = name ? `Anthony and Mai's Wedding - ${name}` : "Anthony and Mai's Wedding" ;
    return (
        <>
            <meta name="theme-color" content="#ffdbdf" />
            <title>{title}</title>

            <meta name="google-site-verification" content="SbiqFmewQ_w30Lsl2Zp-mqbTVtKi3Ys4cfPXNK_3Q54" />
            <meta name="msvalidate.01" content="989EA68844EF579051BDC7DF878C4E3A" />

            <meta name="description" content={description} />

            <meta property="og:url" content={`https://anthonyandmaiwang.github.io${path}`} />
            <meta property="og:site_name" content={title} />
            <meta property="og:locale" content="en_US" />
            <meta property="og:type" content="website" />
            <meta property="og:description" content={description} />
            <meta property="og:image" content={"https://anthonyandmaiwang.github.io/og.jpeg"} />
            <meta property="og:image:alt" content="Anthony and Mai's Wedding" />
            <meta property="og:image:type" content="image/jpeg" />
            <meta property="og:image:width" content="1246" />
            <meta property="og:image:height" content="714" />
        </>
    )
}

export default CommonHead;